/* css reset */

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

*::after,
*::before {
    box-sizing: border-box;
}

#text {
    padding-left: 20px;
    padding-bottom: 20px;
    height: 50vh;
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    overflow-y: auto;
}

#skin {
    height: 50vh;
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
}

.buttons {
    position: fixed;
    top: 50px;
    right: 50px;
    display: flex;
    flex-direction: column;
}

.button {
    width: 50px;
    height: 40px;
    margin-bottom: 20px;
}

@keyframes wave {
    0% {
        transform: rotate(0deg);
    }
    33% {
        transform: rotate(5deg);
    }
    66% {
        transform: rotate(-5deg)
    }
    100% {
        transform: rotate(0deg);
    }
}

.nose:hover {
    transform-origin: center center;
    /* 指定动画起始点 */
    animation: wave 300ms infinite linear;
}